.App {
	display: flex;
	flex-flow: column;
	min-height: 100vh;
	background-color: #d1e8e2;
}

h2,
h3,
h4 {
	text-align: center;
	margin-bottom: 20px;
}
#photo {
	border-radius: 60px;
	width: auto;
	height: 150px;
}
#appointmentsHeader {
	text-align: center;
	margin-bottom: 30px;
}

.about-us {
	display: flex;
	justify-content: space-around;
	width: auto;
	text-align: center;
	margin-top: 50px;
	margin-bottom: 40px;
}
.about-us img {
	height: 150px;
	width: auto;
}

.btn-profile {
	margin-right: 20px;
	margin-bottom: 30px;
	font-size: small;
}

.btn-specialist {
	margin-right: 10px;
	margin-bottom: 5px;
	font-size: small;
}

.user-card-full {
	overflow: hidden;
}
.text-general {
	text-align: center;
}
.text-general h5 {
	padding: 10px;
}
.card {
	border-radius: 5px;
	-webkit-box-shadow: 0 1px 20px 0 rgba(69, 90, 100, 0.08);
	box-shadow: 0 1px 20px 0 rgba(69, 90, 100, 0.08);
	border: none;
	margin-bottom: 30px;
}

.m-r-0 {
	margin-right: 0px;
}

.m-l-0 {
	margin-left: 0px;
}

.user-card-full .user-profile {
	border-radius: 5px 0 0 5px;
}

.bg-c-lite-green {
	background: -webkit-gradient(linear, left top, right top, from(#f29263), to(#ee5a6f));
	background: linear-gradient(to right, #ee5a6f, #f29263);
}

.user-profile {
	padding: 20px 0;
}

.card-block {
	padding: 1.25rem;
}

.m-b-25 {
	margin-bottom: 30px;
}

.img-radius {
	border-radius: 5px;
}

h6 {
	font-size: 14px;
}

.card .card-block p {
	line-height: 25px;
}

@media only screen and (min-width: 1400px) {
	p {
		font-size: 14px;
	}
}

.card-block {
	padding: 1rem;
}

.b-b-default {
	border-bottom: 1px solid #e0e0e0;
}

.m-b-20 {
	margin-bottom: 20px;
}

.p-b-5 {
	padding-bottom: 5px !important;
}

.card .card-block p {
	line-height: 25px;
}

.m-b-10 {
	margin-bottom: 10px;
}

.text-muted {
	color: #919aa3 !important;
}

.b-b-default {
	border-bottom: 1px solid #e0e0e0;
}

.f-w-600 {
	font-weight: 600;
}

.m-b-20 {
	margin-bottom: 20px;
}

.m-t-40 {
	margin-top: 20px;
}

.p-b-5 {
	padding-bottom: 5px !important;
}

.m-b-10 {
	margin-bottom: 10px;
}

.m-t-40 {
	margin-top: 20px;
}

.add {
	border-radius: 20px;
}

.card-y {
	border: solid#ffcb9a;
	border-radius: 20px;
	transition: all 1s;
	cursor: pointer;
}

.card-y:hover {
	-webkit-box-shadow: 3px 5px 17px -4px #777777;
	box-shadow: 3px 5px 17px -4px #777777;
}

#log-out {
	width: auto;
	height: 300px;
	padding-left: 400px;
	display: inline-block;
}

.card-y img {
	border-radius: 20px;
	height: 70px;
	width: auto;
}
